body {
  margin: 0;
  padding: 0;
  font-family: 'Josefin Sans', sans-serif;
  background-image: url(./background.jpg);
  height: 100vh;
  width: 100vw;
  background-size: 100% 100%; }

#root {
  width: 100%;
  height: 100%;
  display: flex; }

li {
  display: flex;
  justify-content: space-between; }

h1 {
  text-align: center; }

ul {
  padding: 0;
  overflow-x: scroll;
  height: 60vh; }

.dropzone {
  margin: auto; }
